<template>
  <div class="container">
    <input type="text" v-model="test1" @blur="test(1)" placeholder="电话" />
    <div class="model">{{test1}}</div>
    <hr />

    <input type="text" v-model="test2" @blur="test(2)" placeholder="邮箱" />
    <div class="model">{{test2}}</div>
    <hr />

    <input type="text" v-model="test3" @blur="test(3)" placeholder="邮箱" />
    <div class="model">{{test3}}</div>
    <hr />
    <!-- <div class="top">1</div>
    <div v-if="btn" id="btns" class="button">确定</div>-->

    <div class="tips">1. 电话验证 /[0-9]|\(|\)|\-/;</div>
    <div class="tips">2. 邮箱验证 /\@([A-Za-z0-9_\-\.])+\./;</div>
    <div class="tips">3. 邮箱校验 /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/</div>

    <input type="text" v-model="test4" @blur="test(4)" placeholder="电话" />
    <div class="model">{{test4}}</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      btn: true,
      test1: "",
      test2: "",
      test3: "",
      test4: ""
    };
  },
  created() {},
  mounted() {
    // this.androidKeyboard();
  },
  methods: {
    test(n) {
      let model = /[0-9]|\(|\)|\-/;
      let mail = /\@([A-Za-z0-9_\-\.])+\./;
      let pattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z])$/;
      if (n == 1) {
        console.log(this.test1);
        console.log(model.test(this.test1));
        if (!model.test(this.test1)) {
          this.test1 =
            "投保人固定电话只允许包含半角的数字、英文括号、“-”符号！";
        } else {
          this.test1 = "true";
        }
      }
      if (n == 2) {
        console.log(mail.test(this.test2));
        if (!mail.test(this.test2)) {
          this.test2 = "投保人电子邮箱必须同时包含半角的 “@”和 “.”！";
        } else {
          this.test2 = "true";
        }
      }
      if (n == 3) {
        console.log(pattern.test(this.test3));
        if (!pattern.test(this.test3)) {
          this.test3 = "投保人电子邮箱在“@”后面，必须有“.”！";
        } else {
          this.test3 = "true";
        }
      }
      if (n == 4) {
        console.log(/\S/.test(this.test4));
        if (/\S/.test(this.test4)) {
          this.test4 = "true";
        } else {
          this.test4 = "false";
        }
      }
    },
    androidKeyboard() {
      let h =
        document.documentElement.clientHeight || document.body.clientHeight;
      window.onresize = function() {
        if (
          document.documentElement.clientHeight < h ||
          document.body.clientHeight < h
        ) {
          document.getElementById("btns").style.display = "none";
        } else {
          document.getElementById("btns").style.display = "block";
        }
      };
    }
  }
};
</script>
<style lang='scss' type='text/scss' scoped>
.container {
  box-sizing: border-box;
  text-align: center;
  input {
    font-size: 0.3rem;
    width: 80%;
    margin: 0.2rem auto;
    border: 1px solid #cccccc;
    padding: 0.1rem;
  }
  p {
    padding: 0.1rem;
    background: rgba(0, 0, 0, 0.2);
    text-align: left;
  }
  .top {
    width: 100%;
    height: 3rem;
    background: #000;
    color: #cccccc;
    font-size: 1rem;
  }
  .model {
    background: #21ae7b;
    color: aliceblue;
    font-size: 0.3rem;
  }
  .button {
    position: fixed;
    width: 100%;
    bottom: 0;
    height: 1rem;
    background: #21ae7b;
    line-height: 1rem;
    text-align: 1rem;
    color: #cccccc;
    font-size: 0.4rem;
  }
  .tips {
    padding: 0.2rem;
    text-align: left;
  }
}
</style>